export default {
	data() {
		return {
			userLogin:{},
			birthday:''
		};
	},
	onLoad() {
		this.userLogin=uni.getStorageSync('userLogin')
		this.birthday=this.userLogin.birthday
	},
	methods:{
		submitBirthday(e){
			this.userLogin.birthday=this.birthday
			uni.setStorageSync('userLogin',this.userLogin)
			uni.navigateBack({
				delta:1
			})
		},
		changeBirthday(e){
			this.birthday=e.detail.value
		}
	}
};
